import React, { useContext, useEffect, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Link, useNavigate } from 'react-router-dom';
import { Row, Navbar, Collapse, Nav, NavItem, NavLink, NavbarToggler, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

import { AppContext } from '../../contexts/app';
import api from '../../services/api';

// import logo from '../../assets/img/logo.jpg';
import './styles.css';

function NavbarComponent() {
  const navigate = useNavigate();
  const [categorias, setCategorias] = useState([]);
  const { navbarTogglerOpen, setNavbarTogglerOpen } = useContext(AppContext);

  useEffect(() => {
    const toggleNavbar = () => {
      const elem = document.getElementById('navbar-top-fixed');
      if (elem) {
        if (
          document.body.scrollTop > 150
          || document.documentElement.scrollTop > 150
        ) {
          elem.style.top = '0';
        } else {
          elem.style.top = '-220px';
        }
      }
    };
    document.addEventListener('scroll', toggleNavbar, false);

    setNavbarTogglerOpen(!isMobile);

    return () => document.removeEventListener('scroll', toggleNavbar, false);
  }, []);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await api.get('/categorias');
        setCategorias(data);
      } catch (error) {
        console.log(error);
      }
    })();
  }, []);

  return (
    <>
      <Row
        id="navbar-top-fixed"
        className="bg-white d-flex justify-content-center px-1 py-2 px-sm-3 py-sm-4"
      >
        {/* <img
          src={logo}
          alt=""
          style={{
            width: '100%',
            height: '100%',
            maxWidth: '300px',
          }}
        /> */}
        <Navbar>
          <Collapse isOpen>
            <Nav>
              <div className="d-flex flex-wrap align-items-center justify-content-center">
                <NavItem>
                  <NavLink href="/#home">Início</NavLink>
                </NavItem>
                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret>
                    Anúncio
                  </DropdownToggle>
                  <DropdownMenu>
                    {categorias.map(
                      (categoria) => !categoria.categoria && (
                      <DropdownItem key={categoria._id}>
                        <Link to={`/${categoria._id}`} key={categoria._id}>
                          {categoria.name}
                        </Link>
                      </DropdownItem>
                      ),
                    )}
                  </DropdownMenu>
                </UncontrolledDropdown>
                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret>
                    Cotações
                  </DropdownToggle>
                  <DropdownMenu>
                    <DropdownItem onClick={() => navigate('/cotacao-novilha')}>
                      Cotação Novilha
                    </DropdownItem>
                    <DropdownItem onClick={() => navigate('/cotacao-boigordo')}>
                      Cotação Boi Gordo
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
                <NavItem>
                  <NavLink href="/#">Sobre Nós</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="/#">Suporte</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="/depoimentos">
                    Depoimentos dos Clientes
                  </NavLink>
                </NavItem>
              </div>
            </Nav>
          </Collapse>
        </Navbar>
      </Row>
      <Row
        className={`align-items-center ${
          navbarTogglerOpen
            ? 'justify-content-center px-4 py-1'
            : 'justify-content-between p-0'
        } justify-content-lg-between px-sm-4`}
        style={{
          borderTop: '1px solid var(--gray)',
        }}
      >
        {/*  <img
          src={logocompleto}
          alt=""
          style={{
            width: '100%',
            height: '100%',
            maxWidth: '300px',
          }}
        /> */}
        <Navbar light>
          <Collapse isOpen={navbarTogglerOpen}>
            <Nav>
              <NavItem>
                <NavLink href="/#home">Início</NavLink>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Anúncio
                </DropdownToggle>
                <DropdownMenu right>
                  {categorias.map(
                    (categoria) => !categoria.categoria && (
                    <DropdownItem key={categoria._id}>
                      <Link to={`/${categoria._id}`} key={categoria._id}>
                        {categoria.name}
                      </Link>
                    </DropdownItem>
                    ),
                  )}
                </DropdownMenu>
              </UncontrolledDropdown>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Cotações
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem onClick={() => navigate('/cotacao-novilha')}>
                    Cotação Novilha
                  </DropdownItem>
                  <DropdownItem onClick={() => navigate('/cotacao-boigordo')}>
                    Cotação Boi Gordo
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </Collapse>
        </Navbar>
        <Navbar>
          <NavbarToggler
            className="d-sm-none"
            onClick={() => setNavbarTogglerOpen((prevState) => !prevState)}
          />
          <Collapse isOpen={navbarTogglerOpen}>
            <Nav>
              <NavItem>
                <NavLink href="/#">Sobre Nós</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/#">Suporte</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/depoimentos">Depoimentos dos Clientes</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/login">
                  <span className="p-0 p-sm-2 d-sm-none">
                    Acessar Conta
                    <i className="fas fa-user-cog p-1" />
                  </span>
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </Row>
    </>
  );
}

export default NavbarComponent;
