import React from 'react';
import NumberFormat from 'react-number-format';

export default function MoneyFormat({ children, value }) {
  return (
    <NumberFormat
      value={children || value}
      defaultValue="0"
      displayType="text"
      decimalSeparator=","
      thousandSeparator="."
      decimalScale={2}
      prefix="R$"
      renderText={(formattedValue) => formattedValue}
      fixedDecimalScale
    />
  );
}
