import React, { useEffect, useState, useContext, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Badge, Container, Row, Button, Col, Card } from 'reactstrap';

import Footer from '../../components/Footer';
import MoneyFormat from '../../components/Formatters/MoneyFormatter';
import Navbar from '../../components/Navbar';
import ReactPlayer from '../../components/ReactPlayer';
import { AppContext } from '../../contexts/app';
import { useAuth } from '../../hooks/auth';
import api from '../../services/api';

function Anuncio() {
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();
  const { setLoading } = useContext(AppContext);
  const params = useParams();
  const [user, setUser] = useState({});
  const [anuncio, setAnuncio] = useState({});
  const [animal, setAnimal] = useState({});
  const [favoritado, setFavoritado] = useState(false);

  useEffect(() => {
    (async () => {
      try {
        if (anuncio && anuncio._id) {
          setLoading(true);
          await api.patch(`/visualizados/${anuncio._id}`);
          console.log('visualizado', anuncio._id);
          setLoading(false);
        }
      } catch (error) {
        setLoading(false);
        console.log(error);
      }
    })();
  }, [anuncio, anuncio._id]);

  useEffect(() => {
    (async () => {
      try {
        setLoading(true);
        const { id } = params;
        const { data } = await api.get(`/animals/${id}`);
        setAnimal(data);
        if (data.anuncio) {
          const anuncioData = {
            ...data.anuncio,
            visualizado: data.anuncio.visualizado + 1,
          };
          setAnuncio(anuncioData);
        }
        setUser(data.user);
        setLoading(false);
      } catch (error) {
        setLoading(false);
        console.log(error);
      }
    })();
  }, []);

  useEffect(() => {
    if (isAuthenticated) {
      try {
        (async () => {
          await api.get(`/favoritados/${anuncio._id}`);
          setFavoritado(true);
        })();
      } catch (error) {
        setFavoritado(false);
        console.log(error);
      }
    }
  }, [isAuthenticated]);

  const handleFavoritar = useCallback(() => {
    try {
      (async () => {
        await api.patch(`/favoritados/${anuncio._id}`);
      })();
    } catch (error) {
      console.log(error);
    }
  }, []);

  return (
    <Container fluid>
      <Navbar />
      <Row className="bg-secondary text-white justify-content-between px-2 px-md-5 py-4">
        <h3>Detalhes do anúncio</h3>
      </Row>
      <Container className="my-3">
        <Row>
          <Col>
            <Container>
              <Row className="pt-5">
                {animal?.categorias?.map((categoria) => (
                  <h5 key={categoria._id}>
                    <Badge color="secondary" className="mr-1">
                      {categoria.name}
                    </Badge>
                  </h5>
                ))}
              </Row>
              <Row className="align-items-center justify-content-between">
                <div>
                  <h3>{anuncio.titulo}</h3>
                  <p>
                    Postado por
                    {' '}
                    <strong>{user.name}</strong>
                    {' '}
                    {anuncio?.createAt
                      ? new Intl.DateTimeFormat('pt-BR', {
                        weekday: 'long',
                        year: 'numeric',
                        month: 'long',
                        day: 'numeric',
                      }).format(new Date(anuncio.createAt))
                      : null}
                    {' '}
                    - Visualizações
                    {' '}
                    <strong>{anuncio.visualizado}</strong>
                  </p>
                </div>
                <div>
                  <Row>
                    <h5 className="text-secondary">
                      <MoneyFormat>{animal.preco}</MoneyFormat>
                    </h5>
                    {`(${animal?.unidade?.name})`}
                  </Row>
                  {isAuthenticated && user.whatsapp ? (
                    <Row>
                      <a
                        title="Whatsapp"
                        href={`https://api.whatsapp.com/send?phone=55${user.whatsapp}&text=Tudo bem ${user.name}, peguei seu contato no gado na mão, sobre este anúncio (${anuncio.titulo}) ${process.env.REACT_APP_URL}/anuncio/${params.id}. Vamos fazer negócios?`}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        <Button color="success">
                          <i className="fab fa-whatsapp mr-2" />
                          CHAMAR NO WHATSAPP
                        </Button>
                      </a>
                    </Row>
                  ) : (
                    <Row>
                      <Button
                        color="success"
                        onClick={() => navigate('/cadastro')}
                      >
                        <i className="fab fa-whatsapp mr-2" />
                        CADASTRE-SE PARA CHAMAR NO WHATSAPP
                      </Button>
                    </Row>
                  )}
                </div>
              </Row>
            </Container>
          </Col>
        </Row>
        <Row>
          <Col xs={12} md={8}>
            <Container>
              <Row className="align-items-center py-3">
                <i className="far fa-file-alt" />
                <a href="#descricao">
                  <Button color="link" className="mr-3">
                    Descrição
                  </Button>
                </a>
                {animal.video ? (
                  <>
                    <i className="fas fa-video" />
                    <a href="#video">
                      <Button color="link" className="mr-3">
                        Vídeo
                      </Button>
                    </a>
                  </>
                ) : null}
                {animal?.images?.length ? (
                  <>
                    <i className="far fa-images" />
                    <a href="#imagens">
                      <Button color="link">Imagens</Button>
                    </a>
                  </>
                ) : null}
              </Row>
              <Row id="descricao">
                <h5 className="mt-5">Descrição</h5>
              </Row>
              <Row className="justify-content-between mt-3">
                <span>
                  <strong>Preço:</strong>
                  {' '}
                  <MoneyFormat>{animal.preco}</MoneyFormat>
                  {`(${animal?.unidade?.name})`}
                </span>
                {animal?.idade?.name ? (
                  <span>
                    <strong>Idade:</strong>
                    {' '}
                    {`(${animal?.idade?.name})`}
                  </span>
                ) : null}
                <span>
                  {anuncio?.createAt ? (
                    <>
                      <strong>Data:</strong>
                      {' '}
                      {new Intl.DateTimeFormat('pt-BR', {
                        weekday: 'long',
                        year: 'numeric',
                        month: 'long',
                        day: 'numeric',
                      }).format(new Date(anuncio.createAt))}
                    </>
                  ) : null}
                </span>
              </Row>
              <Row>
                <h6 className="py-2">{anuncio.titulo}</h6>
              </Row>
              <Row>
                <p>{anuncio.descricao}</p>
              </Row>
              {animal.video ? (
                <>
                  <Row
                    id="video"
                    className="mt-4"
                    style={{
                      borderTop: '1px solid var(--gray)',
                    }}
                  >
                    <h6 className="py-3">Vídeo</h6>
                  </Row>
                  <Row>
                    <ReactPlayer url={animal.video.video_url} />
                  </Row>
                </>
              ) : null}
              {animal.images && animal.images.length ? (
                <Row
                  id="imagens"
                  className="mt-4"
                  style={{
                    borderTop: '1px solid var(--gray)',
                  }}
                >
                  <h6 className="py-3">Imagens</h6>
                  <Row>
                    {animal.images.map((image) => (
                      <Col key={image.id} xs={12} md={6}>
                        <Card>
                          <img
                            src={image.image_url}
                            alt={image.image_url}
                            className="w-100"
                          />
                        </Card>
                      </Col>
                    ))}
                  </Row>
                </Row>
              ) : null}
            </Container>
          </Col>
          <Col xs={12} md={4}>
            <Row className="justify-content-around py-3">
              {/* <Button color="link">
                <i className="fas fa-share-alt mr-1" />
                Compartilhar
              </Button> */}
              {isAuthenticated ? (
                <Button color="link" onClick={handleFavoritar}>
                  <i className={`${favoritado ? 'fas fa-heart' : 'far fa-heart'} mr-1`} />
                  Favoritar
                </Button>
              ) : null}
              {/* <Button color="link">
                <i className="fas fa-exclamation-triangle mr-1" />
                Reportar
              </Button> */}
            </Row>
            {animal?.idade?.name ? (
              <Row className="bg-secondary text-white p-3">
                <h3>
                  <i className="fas fa-bullhorn mr-2" />
                  {animal?.idade?.name}
                </h3>
              </Row>
            ) : null}
            <Row>
              <div
                className="p-3 mt-3 w-100"
                style={{
                  border: '1px solid var(--gray)',
                }}
              >
                <h3>
                  <i className="fas fa-map-marked-alt mr-2" />
                  {animal?.estado}
                </h3>
              </div>
            </Row>
            <Row>
              <div
                className="bg-light p-3 mt-3 w-100"
                style={{
                  border: '1px solid var(--gray)',
                }}
              >
                Dicas de segurança para negócios
              </div>
            </Row>
            <Row
              style={{
                border: '1px solid var(--gray)',
              }}
            >
              <Col>
                <p>
                  <Badge color="info" className="mr-1">
                    1
                  </Badge>
                  {' '}
                  Fique atento com preços irrealistas e outras facilidades.
                </p>

                <p>
                  <Badge color="info" className="mr-1">
                    2
                  </Badge>
                  {' '}
                  Nós do Gado na Mão não pedimos nenhum tipo de código de
                  verificação.
                </p>

                <p>
                  <Badge color="info" className="mr-1">
                    3
                  </Badge>
                  {' '}
                  Certifique-se bem quem é o comprador ou vendedor que você está
                  negociando.
                </p>

                <p>
                  <Badge color="info" className="mr-1">
                    4
                  </Badge>
                  {' '}
                  O Gado na Mão não tem a funcionalidade de participar das
                  negociações. Fazemos somente a intermediação do comprador com
                  o vendedor ou vice versa.
                </p>

                <p>
                  <Badge color="info" className="mr-1">
                    5
                  </Badge>
                  {' '}
                  Embarque os animais após confirmar se o dinheiro entrou na sua
                  conta, assim diminuindo o risco de fraudes.
                </p>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
      <Footer />
    </Container>
  );
}

export default Anuncio;
