import React from 'react';

function Divider({ border, color, width }) {
  return (
    <hr
      style={{
        border: `${border || '1'}px solid var(--${color || 'secondary'})`,
        maxWidth: width || '300px',
        width: width || '100%',
        backgroundColor: `var(--${color})` || 'var(--secondary)',
      }}
    />
  );
}

export default Divider;
